.main-header {
  border-bottom: 2px solid #444343;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  color: #444343;
}

.main-header__title {
  width: 30%;
  margin-right: 0;
  -webkit-animation: App-logo-spin infinite 3s ease;
          animation: App-logo-spin infinite 3s ease;
  line-height: 20px;
  font-size: 30px;
}

.nav {
  margin: 0;
}

.item {
  display: block;
}

.nav__item {
  display: inline-block;
  margin-right: 40px;
  line-height: 56px;
  font-size: 20px;
  font-weight: 500;
  text-transform: uppercase;
  -webkit-transition: all 0.6s;
  -o-transition: all 0.6s;
  transition: all 0.6s;
}

.item:after {
  position: absolute;
  top: 60px;
  left: 0;
  content: "";
  width: 0;
  height: 3px;
  background: #ff5722;
  -webkit-transition: width .3s linear;
  -o-transition: width .3s linear;
  transition: width .3s linear;
}

.item:hover {
  color: #ff5722
}

.item:hover:after {
  width: 100%
}

.item {
  text-decoration: none;
  color: #403f88;
  -webkit-transition: all 0.6s;
  -o-transition: all 0.6s;
  transition: all 0.6s;
}

@-webkit-keyframes App-logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  25% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
  50% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  75% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

@keyframes App-logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  25% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
  50% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  75% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

.main-form {
  width: 20%;
}

.main-form__field {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-align: center;
      align-items: center;
  border: 1px solid #444343;
}

.main-form__movie-title,
.main-form__movie-ratio {
  width: 260px;
}

.wrap-flex {
  display: -ms-flexbox;
  display: flex;
  padding-top: 15px;
}


